@import '../variables.scss';

.joke {
    place-self: center;
}
.joke-div {
    width: 200px;
    height: 200px;
    overflow-y: scroll;
    border: solid 2px black;
    border-radius: 20px;
    display: contents;
    display: inline-block;
    place-self: center;
    background-color: #3b8b85;
    box-shadow: -1px 1px 13px 0px #282D33;
    margin: 5px;
}
.thumbs {
    @include thumbsAndNumber;
    margin-left: 1vw;
    font-size: x-large;
    cursor: pointer;
}
.number {
    @include thumbsAndNumber;
    margin-right: 1vw;
    width: auto;
    float: right;
    background-color: #FFD54C;
    padding: 3px 7px 3px 7px;
    border-radius: 10%;
}
.the-joke {
    color: #DEEEEC;
    margin: 5%;
    font-family: 'Roboto', sans-serif;
    font-size: x-large;
}